import shortUUID from "short-uuid";

export const Main = ({ Number, Data }) => {
    return (
        <main className="Main">
            <p>Number: {Number()}</p>
            {Data.map(({ name, age, company }) => {
                return (
                    <ul key={shortUUID.generate()}>
                        <li>{name}</li>
                        <li>{age}</li>
                        <li>{company}</li>
                    </ul>
                );
            })}

            {/* <ul>
                {Data.map((value) => {
                    return (
                        <li key={`${value.name}${value.age}`}>
                            <ul>
                                <li>{value.name}</li>
                                <li>{value.age}</li>
                                <li>{value.company}</li>
                            </ul>
                        </li>
                    );
                })}
            </ul> */}
        </main>
    );
};
